import { FunctionComponent, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "@reach/router";
import LanguageMenu from "../LanguageMenu/LanguageMenu";
import MobileMenu from "../MobileMenu/MobileMenu";
import logo from "../../../static/svg/logo.svg";
import AppLink from "../AppLink";

interface HeaderProps {
  title: string;
}

const Header: FunctionComponent<HeaderProps> = () => {
  const { t } = useTranslation("navbar");
  const [navbarTop, setNavbarTop] = useState(0);

  useEffect(() => {
    let prevScrollpos = window.pageYOffset;
    window.onscroll = () => {
      const currentScrollPos = window.pageYOffset;
      if (prevScrollpos < currentScrollPos && currentScrollPos > 120) {
        setNavbarTop(-130);
      } else {
        setNavbarTop(0);
      }
      prevScrollpos = currentScrollPos;
    };
  }, []);

  return (
    <div className="relative pt-24">
      <div
        className="flex fixed shadow-xl lg:shadow-none w-full top-0 h-24 justify-center z-50 border-t-4 border-gray-500 bg-gradient-to-b from-sky-500 to-indigo-500"
        id="nav-bar"
        style={{
          transition: "all .25s ease-in-out",
          top: `${navbarTop}px`,
        }}
      >
        <div className="flex h-full text-black container items-center justify-between pl-2">
          <div className="h-full flex items-center">
            <AppLink to="/">
              <h2 className={'font-bold text-4xl underline underline-offset-4'}>Best Taro EU</h2>
            </AppLink>
          </div>
          <div className="flex items-center">
            <div className="w-auto hidden lg:flex">
              <AppLink to="/" className="mx-2 text-xl flex items-center">
                <div id="home" className="py-2 animated-link">
                  <span>{t("navbar:home")}</span>
                </div>
              </AppLink>
              <AppLink to="/about" className="mx-2 text-xl flex items-center">
                <div className="py-2 animated-link">
                  <span>{t("navbar:about")}</span>
                </div>
              </AppLink>
              <AppLink to="/contact" className="mx-2 text-xl flex items-center">
                <div className="py-2 animated-link">
                  <span>{t("navbar:contact")}</span>
                </div>
              </AppLink>
              <AppLink
                to="/feedbacks"
                className="mx-2 text-xl flex items-center"
              >
                <div className="py-2 animated-link">
                  <span>{t("navbar:feedbacks")}</span>
                </div>
              </AppLink>
              <LanguageMenu />
              <MobileMenu />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
